import { Masonry } from '@mui/lab';
import { Box, } from '@mui/material';
import { useNavigate } from 'react-router';

interface ICardItem {
  title: string
  background?: string
  desc: string
  onClick?: () => void
}

export function CardItem({
  title, desc, onClick, background,
}: ICardItem) {
  console.log('-')
  return (
    <Box
      onClick={onClick}
      sx={{
        p: 3,
        opacity: 0.86,
        position: 'relative',
        top: 0,
        left: 0,
        transition: '0.3s',
        background: background || '#000000',
        borderRadius: 2,
        boxShadow: '8px 8px #fff',
        '& > *': {
          maxWidth: 400
        },
        '&:hover': {
          borderRadius: 2,
          transition: '0.3s',
          boxShadow: '15px 15px #fff',
          top: -2,
          left: -2,
          position: 'relative'
        },
      }}
    >
      <h2>
        {title}
      </h2>
      <p>
        {desc}
      </p>
    </Box>
  )
}

